import React, {useState} from 'react';
import Ares from './Ares'
import Aedos from './Aedos';
import royal from './royal';
import princess from './princess';
import selene from './selene';

export default function Tree()
{
    const [family, setFamily] = useState("");
    const [width, setWidth] = useState("");
    if (width === "block")
    {
        window.scrollTo(200, 0);
        //setFamily("Aedos");
    }
    if (family === "")
    {
        return (<>
            <h7 class="backLinkPeople"><a href="/people" className="blackLink">&#10094; BACK TO PEOPLE</a></h7>
            <img class="imageLoadDelay" id="treePage" src="tree1.jpg" alt="Tree"/>
            <div className = "peopleContainer peopleFlex">
                <h14>Families</h14>
            </div>
            <div className="familyGrid">
                <div className="linkContainer smallGap">
                    <a href="/aedos" className="whiteLink hover-underline-animation largeLetters">Aedos</a>
                </div>
                <div className="linkContainer smallGap">
                    <a href="/ares" className="whiteLink hover-underline-animation largeLetters">Ares</a>
                </div>
                <div className="linkContainer smallGap">
                    <a href="/princess" className="whiteLink hover-underline-animation largeLetters">Princess</a>
                </div>
                <div className="linkContainer smallGap">
                    <a href="/royal" className="whiteLink hover-underline-animation largeLetters">Royal</a>
                </div>
                <div className="linkContainer smallGap">
                    <a href="/selene" className="whiteLink hover-underline-animation largeLetters">Selene</a>
                </div>
            </div>
            <div class="centerAll">
                <div class="socialMediaGrid">
                    <div class="socialMedia1">
                    <h7>
                        <a href="https://www.facebook.com/uclaaxs/" class="hover-underline-animation">FACEBOOK</a>
                    </h7>
                    </div>
                    <div class="socialMedia2">
                        <h7>
                            <a href="https://www.instagram.com/uclaaxs/" class="hover-underline-animation">INSTAGRAM</a>
                        </h7>
                    </div>
                </div>
            </div>
        </>
        )
    }
    else if (family === "Aedos")
    {
        return (<>
            {Aedos()}
        </>)
    }
    else if (family === "Ares")
    {
        return (<>
            {Ares()}
        </>)
    }
    else if (family === "royal")
    {
        return (<>
            {royal()}
        </>)
    }
    else if (family === "princess")
    {
        return (<>
            {princess()}
        </>)
    }
    else if (family === "selene")
    {
        return (
            <>
                {selene()}
            </>
        )
    }
}